.loginForm {
    border: 2px solid coral;
    width: 50vw;
    height: 50vh;
    margin-top: 25vh;
    margin-left: 25vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* width: ; */
}
.loginInput {
    width: 80%;
    margin-bottom: 10px;
    font-size: 2rem;
    padding: 5px;
    text-align: center;
    border-style: dashed    ;
}

.loginInput:focus{
    border: 3px dashed firebrick;
    /* border-color: firebrick; */
}

.loginButton {
    padding: 5px 20px;
    font-size: medium;
    color: white;
    background-color: crimson;
    border-radius: 5px;
    border: 2px solid crimson;
}

.loginButton:hover {
    cursor: pointer;
}

.chat-window{
    width: 100vw;
    height: 100vh;
    background-color: aqua;
    display: flex;
    flex-direction: column;
}

.messages-container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto; 
        /* justify-content: flex-end; */
}

.sendMessageContainer{
    display: flex;
    height: 8vh;
    min-height: 8vh;
}

.sendMessageInput{
    flex-grow: 1;
    padding: 5px;
    font-size: 1.2rem;
}

.sendMessageButton {
    padding: 5px 20px;
    font-size: medium;
    color: white;
    background-color: crimson;
    border-radius: 5px;
    border: 2px solid crimson;
}